*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
}
//переменные для боди
:root {
  --list-gap: 30px;
}

/* Container */
.container {
  text-align: center;
  align-content: center;
  align-items: center;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  @media (min-width: 320px) {
    max-width: 480px;
    min-width: 320px;
    // padding-left: 15px;
    // padding-right: 15px;
  }

  @media (min-width: $max-mobile) {
    max-width: 768px;
  }

  @media (min-width: $max-tablet) {
    max-width: 1024px;
  }
}

.filmoteka {
  padding-top: 60px;
  padding-bottom: 50px;
  padding-left: 71px;
  padding-right: 71px;
}

.filmoteka__set {
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;

  @media (min-width: $min-tablet) and (max-width: $max-tablet) {
    display: flex;
    flex-wrap: wrap;
    margin-top: calc(-1 * var(--list-gap));
    margin-left: calc(-1 * var(--list-gap));
  }

  @media (min-width: $min-desktop) {
    margin-left: calc(-1 * var(--list-gap));
    margin-top: calc(-1 * var(--list-gap));
  }
}

.film {
  // padding-left: 600px;
  // margin-right: auto;
  width: 280px;
  transition: $transition-body-card;
  padding: 6px;
  &:hover {
    transform: scale(1.03);
    border-radius: $main-border-radius;
    box-shadow: 0px 5px 10px 2px rgba(54, 54, 54, 0.8);
  }

  &:hover .film-overlay,
  &:focus .film-overlay {
    opacity: 1;
    transform: translateX(0);
    z-index: 2;
    // transition: var(--transition-function);
  }
  @media (max-width: $max-mobile) {
    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }

  @media (min-width: $min-tablet) and (max-width: $max-tablet) {
    max-width: 293px;
    flex-basis: calc(100% / 2 - var(--list-gap));
    margin-top: var(--list-gap);
    margin-left: var(--list-gap);
  }

  @media (min-width: $max-tablet) {
    width: 270px;
    flex-basis: calc(100% / 3 - var(--list-gap));
    margin-left: var(--list-gap);
    margin-top: var(--list-gap);
  }
  &__thumb {
    max-width: 100%;
  }
  &__img {
    display: block;
    max-width: 100%;
    height: auto;
  }

  &__info {
    font-family: $main-font;
    font-style: normal;
    font-weight: 500;
    font-size: $main-font-size;
    line-height: 1.33;
    text-align: left;
    margin-top: 10px;
  }
  &__heading {
    //heading
  }
  &__description {
    color: $orange-color;
    margin-top: 7px;
  }
  &__year {
    //__year
    color: $orange-color;
  }
}
.film-overlay {
  position: absolute;
  border-radius: 5px;
  content: '';
  width: 144px;
  height: 25px;
  right: 9px;
  bottom: 60px;

  opacity: 0;
  transition: $transition-body-card;
  transform: translateX(10%);
  z-index: 1;

  &__link {
    display: flex;
  }
  &__text {
    font-family: $main-font;
    color: $orange-color;
    display: inline-flex;
    border-radius: 10px;
    width: 144px;
    height: 100%;
    // background-color: rgb(208, 255, 0);

    align-items: center;
    justify-content: center;
    border-color: $orange-color;
    color: $red-color;
    background-image: -webkit-linear-gradient(45deg, $orange-color 50%, transparent 50%);
    background-image: linear-gradient(45deg, $orange-color 50%, transparent 50%);
    background-position: 100%;
    background-size: 400%;
    background: 1000ms ease-in-out;
    transition: background 1000ms ease-in-out;
    &:hover {
      background-position: 0;
    }
  }
  &__img {
    display: flex;
    // background-color: rgb(174, 0, 255);
    margin-left: 10px;
  }
}

